import { paymentPageUrl } from "../../config";


export function postDonate(params) {
  // url path from GATSBY_PAYMENT_PAGE_URL env
  const url = new URL(paymentPageUrl);
  // add params as query string to url
  Object.keys(params).forEach(key => url.searchParams.append(key, params[key]));
  // reuturn url as string
  window.location.href = url.toString();
}