import React, { useState } from "react";
import Layout from "../components/layout/index";
import SEO from "../components/seo";
import { SectionBox } from "../components/home";
import styleVars from "../styles/style-vars";
import { Link } from "gatsby";
import { Donate } from "../components/product";
import { shareAthleteStory } from "../lib/utils";

const handleScroll = isModalOpen => {
  if (isModalOpen === true) {
    document.documentElement.style.overflow = "hidden";
  } else {
    document.documentElement.style.overflowY = "scroll";
  }
};

const handleDonate = (donate, setDonate) => {
  setDonate(donate);
  if (donate) {
    document.documentElement.style.overflow = "hidden";
  } else {
    document.documentElement.style.overflowY = "scroll";
  }
};

const About = props => {
  const [isContactOpen, setContact] = useState(false);
  const [donate, setDonate] = useState(false);
  const handleContact = () => {
    handleScroll(!isContactOpen);
    setContact(!isContactOpen);
  };

  const descriptionClassName = "f4 f3-ns ma0 mt4 mt10-ns tc tl-l fw-normal";
  const sideClassName = "col-12 col-6-l mt6 mt0-l";
  const sectionClassName = "pt20 pb10 pt30-ns pb30-ns";

  return (
    <Layout headerClass="bg-grey">
      <SEO title="Empowering Athletes: Support, Success, and Community | Athlete Support Network" />
      <div class="main-content">
      {donate ? (
          <Donate
            isOpen={donate}
            handleModal={() => handleDonate(!donate, setDonate)}
          />
        ) : null}
      <div className={`${styleVars.page.xl} mb10`}>
            <div className="pt10 pb10 pt20-ns tc tl-ns pb20-ns">
              <h1 className="pa0 f3 f1-ns center fw-bold">Breaking Barriers, Unlocking Opportunities</h1>
              <p className="f4 f3-ns ma0 mt4 mt10-ns fw-normal">
              We understand that financial constraints can sometimes hinder your 
              progress as an athlete. That's why Athlete Support Network is here
               to help. Our platform connects you with individuals and organizations
                willing to provide financial assistance. By harnessing the power
                of our community, you can overcome financial barriers, access the
                resources you need, and unlock new opportunities for growth and success.
              </p>
            </div>
          </div>
          <div
            className={`${styleVars.page.xl} ${sectionClassName}`}
            id="how-it-works"
          >
            <h1 className={`ma0 pa0 f3 f2-ns tc fw-600`}>Our Athletes</h1>
            <p className={`f4 f3-ns mt4 mt10-ns tc fw-normal mw-60 center`}>
            At Athlete Support Network, we are honored to introduce you to our
             exceptional athletes. These individuals embody the spirit of 
             dedication, resilience, and unwavering passion for their sports.
              Their stories inspire us and remind us of the incredible power 
              of pursuing our dreams. Get to know some of the remarkable 
              athletes who are part of our community:
            </p>
            <SectionBox
            heading="Laxmi KM"
            sideImg="laxmi.jpg"
            headingClass="ma0 pa0 f2 f1-ns tc tl-l fw-bold"
            bodyClass="o1 o1-l col-12 col-5-l flex flex-column"
            sideImgClassName="hero-image"
            sideClass={`${sideClassName} o2 o2-l col-7-l justify-self-center justify-self-end-r`}
            id="laxmi"
            className={sectionClassName}
          >
            <p className={descriptionClassName}>
            Laxmi, a talented athlete hailing from Uttarakhand, 
            is making a significant impact in the world of athletics. 
            Guided by renowned coach Ranbir Singh Sidhu, she has achieved remarkable success.
             With gold medals in the women's half-marathon and the Khelo India 10,000m race, Laxmi's determination knows no bounds and seeking for an international gold medal.
<br/>
<br/>
Driven by her family's daily wage work, Laxmi is fueled to succeed and overcome challenges. In her pursuit of excellence, she seeks a GPS watch to enhance her training and elevate her performance. By supporting Laxmi on her journey, you become part of her inspiring story and contribute to her reaching new heights in her athletic career.

            </p>
            <button
                  className="btn-base btn-contained f5"
                  style={{
                    minWidth: 280,
                    height: 60,
                    marginTop: 40,
                  }}
                  onClick={() => handleDonate(!donate, setDonate)}
                >
                  Support Laxmi Now
                </button>

                <button
                  className="btn-base btn-outlined f5"
                  style={{
                    minWidth: 280,
                    height: 60,
                    marginTop: 40,
                  }}
                  onClick={() => shareAthleteStory()}
                >
                  Share
                </button>
          </SectionBox>
          </div>
      </div>
    </Layout>
  );
}

export default About;
