import React, { Component } from "react";
import Icon from "../Icon";
import { postDonate } from "./api";
import { InputWrapper, emailRegex, regexPhone } from "../../lib/utils";

const USER_TYPE = Object.freeze({
  SENDER: "SENDER",
  MERCHANT: "MERCHANT",
});

class Donate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      success: false,
      requestError: false,
      values: {
        contactName: "",
        contactNumber: "",
        email: this.props.email ? this.props.email : "",
        userType: USER_TYPE.SENDER,
        amount: "",
        message: "",
      },
      errors: {
        contactName: false,
        contactNumber: false,
        userType: false,
        email: false,
        amount: false,
      },
      errorMsg: {
        contactName: "",
        contactNumber: "",
        userType: "",
        email: "",
        amount: "",
      },
    };
  }

  handleSave = () => {
    if (this.isValid()) {
      this.setState({ loading: true });
      const { values } = this.state;
      const data = {
        payer_name: values.contactName,
        payer_email: values.email,
        payer_phone: values.contactNumber,
        amount: values.amount * 100,
      };
      postDonate(data).then((res) => {
        if (res.status === 200) {
          this.setState({
            loading: false,
            success: true,
          });
        } else {
          this.setState({
            loading: false,
            requestError: true,
          });
        }
      });
    }
  };

  isValid = () => {
    const { email, contactName, contactNumber, amount, userType } =
      this.state.values;
    var errors = false;
    var newState = { ...this.state };
    if (emailRegex.test(email) === false) {
      errors = true;
      newState = {
        ...newState,
        errors: { ...newState.errors, email: true },
        errorMsg: {
          ...newState.errorMsg,
          email: "Please enter a valid email",
        },
      };
    }
    if (!userType) {
      errors = true;
      newState = {
        ...newState,
        errors: { ...newState.errors, userType: true },
        errorMsg: {
          ...newState.errorMsg,
          userType: "Please fill out this field",
        },
      };
    }
    if (!contactNumber || regexPhone.test(contactNumber) === false) {
      errors = true;
      newState = {
        ...newState,
        errors: { ...newState.errors, contactNumber: true },
        errorMsg: {
          ...newState.errorMsg,
          contactNumber: "Please enter a valid 10 digit mobile number",
        },
      };
    }
    if (!contactName) {
      errors = true;
      newState = {
        ...newState,
        errors: { ...newState.errors, contactName: true },
        errorMsg: {
          ...newState.errorMsg,
          contactName: "Please fill out this field",
        },
      };
    }
    if (!amount || amount < 10) {
      errors = true;
      newState = {
        ...newState,
        errors: { ...newState.errors, amount: true },
        errorMsg: {
          ...newState.errorMsg,
          amount: "Please enter a min amount of ₹10",
        },
      };
    }
    this.setState(newState);
    return !errors;
  };

  handleChange = (name, value) => {
    var newState = {
      ...this.state,
      values: { ...this.state.values, [name]: value },
      errors: { ...this.state.errors, [name]: false },
      errorMsg: { ...this.state.errorMsg, [name]: "" },
    };
    this.setState(newState);
  };

  render() {
    const { isOpen, handleModal } = this.props;
    const { loading, values, errors, errorMsg, requestError, success } =
      this.state;
    const modalRoot = isOpen ? "modal-root" : "modal-root hide";
    return (
      <div className={modalRoot}>
        <div
          className="modal-main flex flex-column"
          style={{
            width: "100vw",
            maxWidth: 456,
            minHeight: Math.min(
              typeof window !== "undefined" ? window.innerHeight : 0,
              700
            ),
          }}
        >
          <div className="flex justify-between align-center">
            <h1 className="f3 ma0 fw-bold">Getting Started</h1>
            <Icon
              name="multiply"
              className="ma0 pa0 mh2 cp"
              onClick={handleModal}
            />
          </div>
          <p className="f5 secondary-text-color mt4 mb0">
            Note: We do not share your personal data with any third party.
          </p>
          {success ? (
            <div
              style={{
                flex: "1 1 auto",
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
              }}
            >
              <Icon
                name="blue-check-contained.svg"
                className="db center"
                style={{ height: 64 }}
              />
              <p className="f4 ma0 tc mt8">
                We've successfully received your request. We'll get back to you
                shortly.
              </p>
            </div>
          ) : (
            <div>
              <InputWrapper
                label="Contact Name"
                type="text"
                validMessage={errors.contactName ? errorMsg.contactName : null}
                value={values.contactName}
                rootClassName="mt8"
                inputClass="large"
                onChange={(event) =>
                  this.handleChange("contactName", event.target.value)
                }
              />
              <InputWrapper
                label="Contact Number"
                type="text"
                pattern="\d*"
                rootClassName="mt8"
                inputClass="large"
                validMessage={
                  errors.contactNumber ? errorMsg.contactNumber : null
                }
                value={values.contactNumber}
                onChange={(event) =>
                  this.handleChange("contactNumber", event.target.value)
                }
              />
              <InputWrapper
                label="Email"
                // placeholder="Please enter your email"
                type="text"
                rootClassName="mt8"
                inputClass="large"
                validMessage={errors.email ? errorMsg.email : null}
                value={values.email}
                onChange={(event) =>
                  this.handleChange("email", event.target.value)
                }
              />
              <InputWrapper
                label="Amount"
                type="text"
                pattern="\d*"
                rootClassName="mt8"
                inputClass="large"
                validMessage={errors.amount ? errorMsg.amount : null}
                value={values.amount}
                onChange={(event) =>
                  this.handleChange("amount", event.target.value)
                }
              />
              {requestError ? (
                <p className="mt2 f5 danger-red">
                  An error occured while processing. Please try again later.
                </p>
              ) : null}
              <p className="ma0 f5 secondary-text-color mt4">
              Upon submission, It will open a secure payment link. Please
                complete the payment to show your support.
              </p>
              <div className="mt4 tr mb8">
                <button
                  className="btn-base btn-contained pl6 pr6 f6 tc"
                  style={{
                    height: 40,
                    ...(loading && {
                      opacity: "0.5",
                      transition: "opacity .2s",
                    }),
                  }}
                  onClick={this.handleSave}
                >
                  {loading ? "Loading..." : "Support Now"}
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

export default Donate;
